.box {
    display: flex;
    align-items: center;
    justify-content: center;
    div.card {
        width: 36%;
        height: auto;
        min-height: 150px;
    }
}

.unir {
    display: inline-block;
    font-size: 28px;
    color: var(--ion-color-primary);
    text-align: center;
}

.puntosTiempo {
    text-align: center;
    border-radius: 1rem;
    font-weight: bold;
    color: red;
}

.quiz_puntuacion {
    text-align: center;
    border-radius: 1rem;
    font-weight: bold;
}

.card {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 2px solid rgba(0, 0, 0, 0.125);
    border-radius: 0.75rem;
    align-items: center;
    justify-content: center;
    .card_autor {
        font-size: xx-small;
        background-color: black;
        color: #fff;
        width: 100%;
        border: 1px;
        padding: 2px 1px;
    }
}

.card_img_top {
    width: 100%;
    border-top-left-radius: calc(0.25rem - 1px);
    border-top-right-radius: calc(0.25rem - 1px);
}

.card_body {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    padding: 5px;
    width: 100% !important;
    text-align: center;
    font-family: "Open Sans Condensed", sans-serif;
    font-weight: 700;
    color: black;
}

.centro_pagina_quiz {
    width: 100vw;
    height: 100vh;
    background-image: url("/assets/images/fondo_pc.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    --background: transparent;
    background-blend-mode: luminosity;
    background-color: azure;
    background-position: center;
}

.bien {
    background-color: greenyellow !important;
}

.mal {
    background-color: red !important;
}

.w100 {
    width: 100%;
}

.wiki_buttom {
    display: inline-flex;
    justify-content: flex-start;
    top: 80px;
}

.caja_respuesta {
    height: 200px;
    width: 100%;
    display: contents;
    .respuesta {
        width: 48%;
        margin: 20px 1%;
    }
}

.text_quiz_center {
    text-align: center;
}

.iconQuizCabecera {
    height: 40px;
    width: 40px;
}

.textoPregunta {
    font-family: "Open Sans Condensed", sans-serif;
    font-size: 54px;
    font-weight: 700;
    line-height: 54px;
    margin: 0 0 0;
    padding: 20px 30px;
    text-align: center;
    text-transform: uppercase;
}

