    //ajusta os valores de iphone 4 aqui
}

@media only screen and (max-width: 360px) and (max-height: 667px) {
    .caja_respuesta {
        height: auto;
        width: 100%;
        display: contents;
        .respuesta {
            width: 98%;
            margin: 5px 1%;
            font-size: 12px;
        }
    }
    .respuestaCorrecta {
        color: #fff;
        font-size: 20px;
        text-align: center;
        background-color: green;
        padding: 10px;
        margin: 10px;
        position: absolute;
        bottom: 60;
    }
    .respuestaIncorrecta {
        padding: 10px;
        margin: 10px;
        //bottom: 60;
    }
    .quiz_puntuacion {
        text-align: center;
        border-radius: 12px;
        font-weight: bold;
        font-size: 20px;
    }
    .iconQuizCabecera {
        height: 3x0px;
        width: 30px;
    }
    .box {
        display: flex;
        align-items: center;
        justify-content: center;
        div.card {
            width: 75%;
            height: auto;
        }
    }
    .text_quiz_center {
        text-align: end;
    }
    .caja_ayuda {
        overflow-y: auto;
        white-space: nowrap;
        height: auto;
    }
    .textoPregunta {
        font-family: "Open Sans Condensed", sans-serif;
        font-size: 22px;
        font-weight: 700;
        line-height: 26px;
        margin: 0 0 0;
        padding: 10px 15px;
        text-align: center;
        text-transform: uppercase;
    }
    .botonContinuar {
        margin: 30px;
        font-size: 30px;
    }
}

